.Scrollable {
  max-height: 250px;
  padding-right: 5px;
  position: relative;
  display: flex;
  flex-flow: column;
  align-items: stretch;
  overflow: hidden; }
  .Scrollable:hover > .scrollbar.auto {
    transition-delay: 0s;
    opacity: 1; }
  .Scrollable .viewport {
    overflow-y: scroll;
    overflow-x: hidden;
    max-height: 100%; }
    .Scrollable .viewport::-webkit-scrollbar {
      display: none; }
  .Scrollable.without-scroll {
    padding-right: 0; }
    .Scrollable.without-scroll > .viewport {
      overflow: inherit;
      padding-right: 0;
      margin-right: 0; }
    .Scrollable.without-scroll > .scrollbar {
      visibility: hidden; }
  .Scrollable .scrollbar {
    position: absolute;
    right: 0px;
    top: 0px;
    width: 5px;
    transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    transition-delay: 0.5s;
    z-index: 1; }
    .Scrollable .scrollbar.auto, .Scrollable .scrollbar.hidden {
      opacity: 0; }
    .Scrollable .scrollbar.visible, .Scrollable .scrollbar:active {
      transition-delay: 0s;
      opacity: 1; }
    .Scrollable .scrollbar .thumb {
      background-color: rgba(0, 0, 0, 0.3);
      width: 5px;
      position: absolute;
      right: 0px;
      top: 0px;
      user-select: none;
      will-change: top;
      border-radius: 2px; }
      .Scrollable .scrollbar .thumb:hover {
        background-color: rgba(0, 0, 0, 0.5); }
      .Scrollable .scrollbar .thumb:active {
        background-color: rgba(0, 0, 0, 0.7); }
