* {
  font-family: Roboto, sans-serif; }

#root {
  min-height: 80vh;
  position: relative;
  overflow: hidden; }
  #root .TextField {
    max-width: 300px; }
  #root .row {
    display: flex;
    flex-flow: row nowrap;
    align-items: center; }
    #root .row.right {
      justify-content: flex-end; }
  #root .column {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start; }
  #root a {
    text-decoration: none;
    color: lightblue; }
  #root .sb-show-main .FormUploadProgress {
    transition-delay: 0s !important;
    bottom: unset;
    right: unset; }
  #root .FloatingActionButton.fab-right-96 {
    right: 96px; }
  #root .AutocompleteSelect.menu {
    min-width: 0; }
    #root .AutocompleteSelect.menu .popup {
      left: 0; }
      #root .AutocompleteSelect.menu .popup .DummyChild {
        font-size: 14px;
        color: #818181; }
      #root .AutocompleteSelect.menu .popup .material-icons {
        color: #818181;
        margin-right: 4px; }
    #root .AutocompleteSelect.menu.active .popup {
      top: 33px; }
    #root .AutocompleteSelect.menu > .selected {
      border: none; }
      #root .AutocompleteSelect.menu > .selected.nolabel {
        border: none; }
      #root .AutocompleteSelect.menu > .selected .material-icons {
        color: #A6A6A6;
        transition: color 0.2s ease; }
      #root .AutocompleteSelect.menu > .selected .ItemWrapper.selected {
        padding-left: 15px;
        margin-left: -5px; }
    #root .AutocompleteSelect.menu.active .selected .material-icons {
      color: #333333; }
  #root .AnimatedHolder .animated {
    transition: all 1s ease; }
  #root .FormUploadProgress {
    max-width: 300px;
    max-height: 200px; }
  #root .DNDExample {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    padding-top: 20px; }
    #root .DNDExample .Draggable {
      display: inline-block; }
      #root .DNDExample .Draggable .item {
        display: inline-block;
        background-color: #00bcd4;
        padding: 10px;
        border-radius: 4px;
        transition: transform 200ms ease; }
      #root .DNDExample .Draggable.dragged .item {
        transform: rotate(10deg); }
    #root .DNDExample .Droppable {
      background-color: #4ac263;
      display: inline-block;
      border-radius: 4px;
      padding: 50px;
      position: absolute;
      left: 300px;
      top: 20px; }
      #root .DNDExample .Droppable.second {
        top: 300px; }
      #root .DNDExample .Droppable.allow {
        background-color: #5bd073; }
      #root .DNDExample .Droppable.hover {
        background-color: #4CAF50; }

.sb-show-main {
  padding: 20px;
  position: relative; }
