.DNDContainer {
  position: relative; }
  .DNDContainer .Draggable[draggable=true], .DNDContainer .Draggable [draggable=true] {
    cursor: pointer; }
  .DNDContainer .Draggable.placeholder {
    opacity: 0.2; }
  .DNDContainer .Draggable.copy {
    position: absolute;
    box-sizing: border-box;
    will-change: transform;
    opacity: 0; }
  .DNDContainer .Draggable.dragged {
    z-index: 10; }
  .DNDContainer .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none; }
